import cx from "classnames";
import React, { memo, useCallback, useMemo } from "react";

import { IDataPoint, IDataPointValue, TaxonomyEntry } from "../../types";

import { StateCallbacks } from "../../state";
import { isDataPointValid } from "../../state/areAnswersValid";
import { AddButton } from "../AddButton";
import { DuplicateButton } from "../DuplicateButton";
import { RemoveButton } from "../RemoveButton";
import { BooleanInput } from "./BooleanInput";
import { IInputComponentProps } from "./common";
import { DateTimeInput } from "./DateTimeInput";
import { EnumInput } from "./EnumInput";
import { NumberInput } from "./NumberInput";
import { PointerInput } from "./PointerInput";
import { StringInput } from "./StringInput";

interface IProps {
  active: boolean;
  dataPoint: IDataPoint;
  disableAdd: boolean;
  disableRemove: boolean;
  index: number;
  isGroupQuestion: boolean;
  groupIndex?: number;
  groupKey?: string;
  label: string;
  review: boolean;
  showLabel: boolean;
  showMultiple: boolean;
  stateCallbacks: StateCallbacks;
  taxonomyEntry: TaxonomyEntry;
  value: IDataPointValue<any>;
}

export const InputComponentMap = {
  boolean: BooleanInput,
  date: DateTimeInput,
  datetime: DateTimeInput,
  enum: EnumInput,
  number: NumberInput,
  pointer: PointerInput,
  selection: StringInput,
  string: StringInput,
  time: DateTimeInput,
};

export const Answer = memo(
  ({
    active,
    dataPoint,
    disableAdd,
    disableRemove,
    groupIndex,
    groupKey,
    index,
    isGroupQuestion,
    label,
    review,
    showLabel,
    showMultiple,
    stateCallbacks,
    taxonomyEntry,
    value,
  }: IProps) => {
    const invalid = useMemo(() => !isDataPointValid(value, taxonomyEntry), [value, taxonomyEntry]);
    const onAddNewAnswer = useCallback(() => {
      if (isGroupQuestion && typeof groupKey === "string" && typeof groupIndex === "number") {
        stateCallbacks.onAddNewAnswer(groupIndex, groupKey, dataPoint.key);
      } else {
        stateCallbacks.onAddNewNonGroupedAnswer(dataPoint.key);
      }
    }, [
      dataPoint.key,
      index,
      isGroupQuestion,
      groupIndex,
      groupKey,
      stateCallbacks.onAddNewAnswer,
      stateCallbacks.onAddNewNonGroupedAnswer,
    ]);
    const onDuplicateAnswer = useCallback(() => {
      if (isGroupQuestion && typeof groupKey === "string" && typeof groupIndex === "number") {
        stateCallbacks.onDuplicateAnswer(groupIndex, groupKey, dataPoint.key, value);
      } else {
        stateCallbacks.onDuplicateNonGroupedAnswer(dataPoint.key, value);
      }
    }, [
      dataPoint.key,
      index,
      isGroupQuestion,
      groupIndex,
      groupKey,
      stateCallbacks.onDuplicateAnswer,
      stateCallbacks.onDuplicateNonGroupedAnswer,
      value,
    ]);
    const onFocus = useCallback(() => {
      if (isGroupQuestion && typeof groupKey === "string" && typeof groupIndex === "number") {
        stateCallbacks.onSelectActiveDataPoint(groupKey, groupIndex, dataPoint.key, index);
      } else {
        stateCallbacks.onSelectActiveNonGroupedDataPoint(dataPoint.key, index);
      }
    }, [
      dataPoint.key,
      index,
      isGroupQuestion,
      groupIndex,
      groupKey,
      stateCallbacks.onSelectActiveDataPoint,
      stateCallbacks.onSelectActiveNonGroupedDataPoint,
    ]);
    const onRemoveAnswer = useCallback(() => {
      if (isGroupQuestion && typeof groupKey === "string" && typeof groupIndex === "number") {
        stateCallbacks.onRemoveAnswer(
          groupKey,
          groupIndex,
          dataPoint.key,
          index,
          taxonomyEntry.type,
        );
      } else {
        stateCallbacks.onRemoveNonGroupedAnswer(dataPoint.key, index, dataPoint.type);
      }
    }, [
      dataPoint.key,
      dataPoint.type,
      index,
      isGroupQuestion,
      groupIndex,
      groupKey,
      stateCallbacks.onRemoveAnswer,
      stateCallbacks.onRemoveNonGroupedAnswer,
      taxonomyEntry.type,
    ]);
    const onUpdateAnswer = useCallback(
      (answerValue: any, answerValueRaw: string) => {
        if (isGroupQuestion && typeof groupKey === "string" && typeof groupIndex === "number") {
          stateCallbacks.onUpdateGroupedAnswer(
            groupKey,
            groupIndex,
            dataPoint.key,
            index,
            answerValue,
            answerValueRaw,
          );
        } else {
          stateCallbacks.onUpdateNonGroupedAnswer(
            dataPoint.key,
            index,
            answerValue,
            answerValueRaw,
          );
        }
      },
      [
        dataPoint.key,
        dataPoint.type,
        index,
        isGroupQuestion,
        groupIndex,
        groupKey,
        stateCallbacks.onUpdateNonGroupedAnswer,
      ],
    );
    let inputComponent = null;
    if (dataPoint.type in InputComponentMap) {
      inputComponent = React.createElement<IInputComponentProps<any>>(
        InputComponentMap[dataPoint.type],
        {
          dataPointKey: dataPoint.key,
          index,
          invalid,
          onFocus,
          onUpdateAnswer,
          showAlerts: false,
          taxonomyEntry,
          value,
        },
      );
    }
    return (
      <div className={cx("grouped-row", { active })}>
        <div className="grouped-column column-support">
          <a
            className={cx({
              "text-danger": value.idRef === null,
              "text-primary": value.idRef !== null,
            })}
            href="#"
            onClick={e => {
              e.preventDefault();
              onFocus();
            }}
            title="Support"
          >
            <i className="fa fa-arrow-left" />
          </a>
        </div>
        {showLabel && (
          <div
            className={cx("grouped-column", "column-label", { "text-danger": invalid })}
            onClick={() => {
              onFocus();
            }}
          >
            {label}
          </div>
        )}
        <div className={cx("grouped-column column-input", { "full-width": !showLabel })}>
          {inputComponent}
        </div>
        {showMultiple && (
          <>
            <div className="grouped-column column-add">
              <AddButton disabled={disableAdd} onClick={onAddNewAnswer} />
            </div>
            <div className="grouped-column column-duplicate">
              <DuplicateButton disabled={disableAdd} onClick={onDuplicateAnswer} />
            </div>
          </>
        )}
        <div className="grouped-column column-remove">
          <RemoveButton clear={disableRemove} onClick={onRemoveAnswer} review={review} />
        </div>
      </div>
    );
  },
);
Answer.displayName = "Answers";
