import React, { useCallback, useMemo } from "react";
import { ListGroupItem } from "reactstrap";

import { StateCallbacks } from "../../state";
import { IDataPoint, IDataPointGroup, ITaxonomy, State } from "../../types";
import { ActiveGroupQuestion } from "./ActiveGroupQuestion";
import { ActiveQuestion } from "./ActiveQuestion";
import { InactiveQuestion } from "./InactiveQuestion";

type Props = Readonly<{
  active: State["activeDataPoint"];
  dataPoint: IDataPoint | IDataPointGroup;
  index: number;
  review: boolean;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

export const Question = ({ active, dataPoint, index, review, stateCallbacks, taxonomy }: Props) => {
  let contents = null;
  if (active.groupKey !== dataPoint.key) {
    contents = (
      <InactiveQuestion
        dataPoint={dataPoint}
        index={index}
        taxonomyEntry={taxonomy[dataPoint.key]}
        taxonomy={taxonomy}
      />
    );
  } else {
    if (dataPoint.isGroup) {
      contents = (
        <ActiveGroupQuestion
          active={active}
          group={dataPoint}
          index={index}
          review={review}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      );
    } else {
      contents = (
        <ActiveQuestion
          dataPoint={dataPoint}
          index={index}
          review={review}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      );
    }
  }
  const onItemClick = useCallback(() => {
    stateCallbacks.onSelectDataPoint(dataPoint.key);
  }, [dataPoint.key, stateCallbacks.onSelectDataPoint]);
  const itemId = useMemo(() => `question-${dataPoint.key}`, [dataPoint.key]);
  return (
    <ListGroupItem id={itemId} onClick={onItemClick}>
      {contents}
    </ListGroupItem>
  );
};
Question.displayName = "Question";
