import React, { memo, useCallback } from "react";
import { Col, Form, Row } from "reactstrap";
import { StateCallbacks } from "../../state";
import { IDataPoint, ITaxonomy } from "../../types";
import { Answer } from "../Answer";
import { QuestionTitle } from "./QuestionTitle";

type Props = Readonly<{
  dataPoint: IDataPoint;
  index: number;
  review: boolean;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

export const ActiveQuestion = memo(
  ({ dataPoint, index, review, stateCallbacks, taxonomy }: Props) => {
    const onSubmit = useCallback(e => {
      e.preventDefault();
    }, []);
    return (
      <Form onSubmit={onSubmit}>
        <QuestionTitle index={index} label={dataPoint.label} />
        <Row>
          <Col className="grouped-container" xs={12}>
            {dataPoint.values.map((v, i) => (
              <Answer
                active
                dataPoint={dataPoint}
                disableAdd={!dataPoint.multiple}
                disableRemove={!dataPoint.multiple || dataPoint.values.length < 2}
                key={`answer-${i}`}
                index={i}
                isGroupQuestion={false}
                label={dataPoint.label}
                review={review}
                showLabel={false}
                showMultiple={dataPoint.multiple}
                stateCallbacks={stateCallbacks}
                taxonomyEntry={taxonomy[dataPoint.key]}
                value={v}
              />
            ))}
          </Col>
        </Row>
      </Form>
    );
  },
);

ActiveQuestion.displayName = "ActiveQuestion";
