import { RefObject, useEffect } from "react";

const SCROLL_PADDING = 75;

export const useScrollToGroup = (activeGroup: string | null, scrollRef: RefObject<HTMLElement>) => {
  useEffect(() => {
    if (!activeGroup) {
      return;
    }
    const group = document.getElementById(`question-${activeGroup}`);
    if (!group) {
      return;
    }
    setTimeout(() => {
      if (!scrollRef.current) {
        return;
      }
      scrollRef.current.style.scrollBehavior = "smooth";
      scrollRef.current.scrollTop = Math.max(0, group.offsetTop - SCROLL_PADDING);
    }, 0);
  }, [activeGroup, scrollRef.current]);
};
