import { State } from "../../types";

export interface IAddCorrectionsAction {
  contentId: string;
  corrections: Array<string>;
  type: "ADD CORRECTIONS";
}

export const addCorrections = (state: State, action: IAddCorrectionsAction): State => {
  return {
    ...state,
    modified: true,
    textCorrections: {
      ...state.textCorrections,
      [action.contentId]: action.corrections,
    },
  };
};
