import React, { memo } from "react";
import { Button, ButtonGroup } from "reactstrap";

import { IInputComponentProps } from "./common";

export const BooleanInput = memo(
  ({
    dataPointKey,
    index,
    invalid,
    onFocus,
    onUpdateAnswer,
    showAlerts,
    value,
  }: IInputComponentProps<boolean>) => {
    const inputId = `input-${dataPointKey}-${index}`;
    return (
      <>
        <ButtonGroup className="boolean-input" id={inputId}>
          <Button
            color="primary"
            onClick={() => {
              onFocus(dataPointKey, index);
              onUpdateAnswer(true, "true");
            }}
            outline={value.value !== true}
            type="button"
          >
            Yes
          </Button>
          <Button
            color="primary"
            onClick={() => {
              onFocus(dataPointKey, index);
              onUpdateAnswer(false, "false");
            }}
            outline={value.value !== false}
            type="button"
          >
            No
          </Button>
        </ButtonGroup>
        {showAlerts && invalid && <small className="text-danger">You must select an option.</small>}
      </>
    );
  },
);
BooleanInput.displayName = "BooleanInput";
