import cx from "classnames";
import React, { SyntheticEvent } from "react";

import { Cell as CellType, State, TableContent } from "../../../types";
import { Props as ContentProps } from "../types";
import { extractTextBlock } from "../util";

type Props = Readonly<{
  cell: CellType;
  fontInformation: State["fontInformation"];
  isHeader: boolean;
  onSelectBlock: ContentProps<TableContent>["onSelectBlock"];
  selectedCell: string | null;
}>;

export const Cell = ({ cell, fontInformation, isHeader, onSelectBlock, selectedCell }: Props) => {
  const contents = extractTextBlock(cell.element, cell.id, fontInformation);
  const domProps = {
    className: cx({ selected: !!selectedCell && selectedCell === cell.id }),
    colSpan: cell.colSpan,
    id: `support-${cell.id}`,
    onClick: (e: SyntheticEvent<HTMLElement>) => {
      if (!cell.id || !onSelectBlock) {
        return;
      }
      e.stopPropagation();
      onSelectBlock(cell.id, cell.text);
    },
    rowSpan: cell.rowSpan,
  };
  return React.createElement(isHeader ? "th" : "td", domProps, contents);
};
Cell.displayName = "Cell";
