import cx from "classnames";
import React, { useEffect, useState } from "react";

import { ContentType, ImageBlock, ImageContent, MathBlock, MathContent } from "../../../types";
import { Props } from "../types";

export const InnodomImage = ({
  content,
  onSelectBlock,
  pages,
  selected,
}: Props<ImageContent | MathContent>) => {
  const blocks: Array<ImageBlock | MathBlock> = content.blocks;
  return (
    <>
      {blocks.map((block, index) => {
        const { height, width, page, x, y } = block;
        const [dataUri, setDataUri] = useState<string | null>(null);
        useEffect(() => {
          const image = new Image();
          image.crossOrigin = "anonymous";
          image.src = pages[page].backgroundSrc;
          image.onload = () => {
            const scale = image.naturalHeight / pages[page].height;
            if (isNaN(scale)) {
              return;
            }
            const canvas = document.createElement("canvas");
            canvas.width = width * scale;
            canvas.height = height * scale;
            const context = canvas.getContext("2d");
            if (!context) {
              return;
            }
            context.drawImage(
              image,
              x * scale,
              y * scale,
              width * scale,
              height * scale,
              0,
              0,
              width * scale,
              height * scale,
            );
            setDataUri(canvas.toDataURL());
          };
        }, []);
        if (dataUri === null) {
          return null;
        }
        const styles = {
          height: "auto",
          maxWidth: "100%",
          width: "auto",
        };
        return (
          <div
            className={cx("block", "d-inline-block", "p-2", { selected })}
            key={`block-${content.id}-${index}`}
            id={`support-${content.id}`}
            onClick={() => {
              if (onSelectBlock) {
                onSelectBlock(
                  content.id,
                  content.type === ContentType.Image ? "Image selected" : "Math selected",
                );
              }
            }}
          >
            <img className="d-block" src={dataUri} style={styles} />
          </div>
        );
      })}
    </>
  );
};
InnodomImage.displayName = "InnodomImage";
