import useLocalStorage from "@rooks/use-localstorage";
import cx from "classnames";
import React, { useCallback, useMemo, useState } from "react";
import ReactResizeDetector from "react-resize-detector";
import { Button, ButtonGroup, Form, FormGroup, Input, Label } from "reactstrap";

import { StateCallbacks } from "../state";
import { getActiveDataPoint, getActiveValue } from "../state/util";
import { ITaxonomy, State } from "../types";
import { Pages } from "./Pages";
import { Text } from "./Text";
import { onSelectText as onSelectTextCallback, TextSelection } from "./TextSelection";

type Props = Readonly<{
  disableEditing: boolean;
  review: boolean;
  state: State;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

type ResizeDetectorCallbackArgs = Readonly<{
  height: number | undefined;
  width: number | undefined;
}>;

type UIState = Readonly<{
  automaticJump: boolean;
  view: "images" | "text";
  zoom: string;
}>;
const DEFAULT_UI_STATE = JSON.stringify({
  automaticJump: false,
  view: "images",
  zoom: "1",
});

export const Document = ({ disableEditing, review, state, stateCallbacks, taxonomy }: Props) => {
  const [persistedUIState, updatePersistedUIState] = useLocalStorage(
    "MAPPING-UI-STATE",
    DEFAULT_UI_STATE,
  );
  const defaultState = useMemo(() => {
    const parsedDefault = JSON.parse(DEFAULT_UI_STATE);
    try {
      const persisted = JSON.parse(persistedUIState);
      if (typeof persisted !== "object" || persisted === null || Array.isArray(persisted)) {
        return parsedDefault;
      }
      return { ...parsedDefault, ...persisted };
    } catch (err) {
      console.error(err);
      return parsedDefault;
    }
  }, [persistedUIState]);
  const [uiState, updateUIState] = useState<UIState>(defaultState);
  const { automaticJump, view, zoom } = uiState;
  const activeValue = getActiveValue(state);
  let selectedBlock: string | null = null;
  if (activeValue !== null) {
    selectedBlock = activeValue.idRef;
  }
  const onSelectBlock = useCallback(
    (id: string, text: string) => {
      if (review) {
        return;
      }
      stateCallbacks.onSelectBlock(id, taxonomy, text);
    },
    [review, stateCallbacks.onSelectBlock, taxonomy],
  );
  const onSelectPageBlock = useCallback(
    (id: string, text: string) => {
      onSelectBlock(id, text);
      if (automaticJump) {
        updateUIState(s => {
          const newState: UIState = { ...s, view: "text" };
          updatePersistedUIState(JSON.stringify(newState));
          return newState;
        });
      }
    },
    [automaticJump, onSelectBlock],
  );
  const onSelectText = onSelectTextCallback(
    getActiveDataPoint(state),
    activeValue,
    state.activeDataPoint,
    stateCallbacks,
  );
  const [confirmTableSelection, updateConfirmTableSelection] = useState(true);
  const disableTableSelectionConfirmation = useCallback(() => {
    updateConfirmTableSelection(false);
  }, [updateConfirmTableSelection]);
  return (
    <div className="d-flex h-100 flex-column overflow-hidden">
      <div className="align-items-center border-bottom border-right d-flex flex-row justify-content-between p-2">
        <ButtonGroup>
          <Button
            color="secondary"
            outline={view !== "images"}
            onClick={() => {
              updateUIState(s => {
                const newState: UIState = { ...s, view: "images" };
                updatePersistedUIState(JSON.stringify(newState));
                return newState;
              });
            }}
            style={{ width: "100px" }}
          >
            <i className="fa fa-columns mr-2" />
            Pages
          </Button>
          <Button
            color="secondary"
            onClick={() => {
              updateUIState(s => {
                const newState: UIState = { ...s, view: "text" };
                updatePersistedUIState(JSON.stringify(newState));
                return newState;
              });
            }}
            outline={view !== "text"}
            style={{ width: "100px" }}
          >
            <i className="fa fa-file-text mr-2" />
            Text
          </Button>
        </ButtonGroup>

        <Form inline>
          <FormGroup>
            <Label htmlFor="zoom">
              Zoom:
              <Input
                className={cx("ml-1", { "mr-3": !review })}
                disabled={view !== "images"}
                id="zoom"
                onChange={e => {
                  const {
                    target: { value },
                  } = e;
                  updateUIState(s => {
                    const newState: UIState = { ...s, zoom: value };
                    updatePersistedUIState(JSON.stringify(newState));
                    return newState;
                  });
                }}
                type="select"
                value={zoom}
              >
                <option value="1">Fit Width</option>
                <option value="1.5">150%</option>
                <option value="2">200%</option>
                <option value="2.5">250%</option>
              </Input>
            </Label>
          </FormGroup>
          {!review && (
            <FormGroup check>
              <Label check>
                <Input
                  type="checkbox"
                  checked={automaticJump}
                  onChange={() => {
                    updateUIState(s => {
                      const newState: UIState = { ...s, automaticJump: !s.automaticJump };
                      updatePersistedUIState(JSON.stringify(newState));
                      return newState;
                    });
                  }}
                />
                Automatically Jump to Text
              </Label>
            </FormGroup>
          )}
        </Form>
      </div>
      <div className="flex-fill position-relative">
        <ReactResizeDetector handleHeight handleWidth>
          {({ height, width }: ResizeDetectorCallbackArgs) => {
            if (height === undefined || width === undefined) {
              return <div />;
            }
            const viewHeight = Math.floor(height);
            const viewWidth = Math.floor(width);
            return (
              <TextSelection onSelectText={onSelectText}>
                <div
                  className="bg-secondary position-absolute"
                  style={{
                    height: `${viewHeight}px`,
                    left: view === "images" ? "0" : `-${viewWidth + 5}px`,
                    top: "0",
                    transition: "left ease-in-out 0.5s",
                    width: "100%",
                  }}
                >
                  <Pages
                    activeDataPoint={state.activeDataPoint}
                    confirmTableSelection={confirmTableSelection}
                    content={state.content}
                    disableTableSelectionConfirmation={disableTableSelectionConfirmation}
                    onSelectBlock={onSelectPageBlock}
                    pages={state.pages}
                    selectedBlock={selectedBlock}
                    viewWidth={viewWidth}
                    zoom={parseFloat(zoom)}
                  />
                </div>
                <div
                  className="bg-white position-absolute"
                  style={{
                    bottom: "0",
                    left: view === "text" ? "0" : `${viewWidth + 5}px`,
                    top: "0",
                    transition: "left ease-in-out 0.5s",
                    width: "100%",
                  }}
                >
                  <Text
                    activeBlock={selectedBlock}
                    activeDataPoint={state.activeDataPoint}
                    confirmTableSelection={confirmTableSelection}
                    content={state.content}
                    contentOrder={state.contentOrder}
                    corrections={state.textCorrections}
                    disableEditing={disableEditing || review}
                    disableTableSelectionConfirmation={disableTableSelectionConfirmation}
                    fontInformation={state.fontInformation}
                    onAddCorrections={stateCallbacks.onAddCorrections}
                    onSelectBlock={onSelectBlock}
                    pages={state.pages}
                  />
                  ;
                </div>
              </TextSelection>
            );
          }}
        </ReactResizeDetector>
      </div>
    </div>
  );
};
Document.displayName = "Document";
