import moment from "moment";
import React, { memo } from "react";
import { DateTimePicker } from "react-widgets";

import { InnodomDateFormats, WorkbenchDateFormats } from "@ui/util/dateFormats";
import {
  IDateTaxonomyEntry,
  IDateTimeTaxonomyEntry,
  ITimeTaxonomyEntry,
  TaxonomyEntryType,
} from "../../types";
import { IInputComponentProps } from "./common";

export const DateTimeInput = memo(
  ({
    dataPointKey,
    index,
    invalid,
    onFocus,
    onUpdateAnswer,
    showAlerts,
    taxonomyEntry,
    value,
  }: IInputComponentProps<Date>) => {
    const entry = taxonomyEntry as IDateTimeTaxonomyEntry | IDateTaxonomyEntry | ITimeTaxonomyEntry;
    const format =
      entry.type in WorkbenchDateFormats
        ? WorkbenchDateFormats[entry.type]
        : WorkbenchDateFormats.datetime;
    const currentValue = moment(value.value);
    const dateValue = currentValue.isValid() ? currentValue.toDate() : undefined;
    return (
      <>
        <DateTimePicker
          date={entry.type === TaxonomyEntryType.Date || entry.type === TaxonomyEntryType.DateTime}
          format={format}
          onChange={(date: Date) => {
            let dateFormat = InnodomDateFormats.datetime;
            if (entry.type in InnodomDateFormats) {
              dateFormat = InnodomDateFormats[entry.type];
            }
            const momentDate = moment(date);
            onUpdateAnswer(momentDate.toString(), momentDate.format(dateFormat));
          }}
          onFocus={() => {
            onFocus(dataPointKey, index);
          }}
          max={
            entry.type === TaxonomyEntryType.Date || entry.type === TaxonomyEntryType.DateTime
              ? entry.max
              : undefined
          }
          min={
            entry.type === TaxonomyEntryType.Date || entry.type === TaxonomyEntryType.DateTime
              ? entry.min
              : undefined
          }
          parse={str => {
            const parsed = moment(str, WorkbenchDateFormats.allSupported);
            if (parsed.isValid()) {
              return parsed.toDate();
            }
            setTimeout(() => {
              onUpdateAnswer(undefined, "");
            }, 0);
            return new Date();
          }}
          placeholder={format.replace(/\\/g, "")}
          time={entry.type === TaxonomyEntryType.Time || entry.type === TaxonomyEntryType.DateTime}
          value={dateValue}
        />
        {showAlerts && invalid && <small className="text-danger">A value must be selected.</small>}
      </>
    );
  },
);
DateTimeInput.displayName = "DateTimeInput";
