import { StateCallbacks } from "../../state";
import { Content, Page, State } from "../../types";

export enum FontClass {
  Smaller = "font-size-smaller",
  Small = "font-size-small",
  Normal = "font-size-normal",
  Large = "font-size-large",
  Larger = "font-size-larger",
}

export interface IFontInformation {
  lowerBound: number;
  mode: number;
  upperBound: number;
}

export type Props<T extends Content> = Readonly<{
  confirmTableSelection: boolean;
  content: T;
  corrections?: State["textCorrections"];
  disableTableSelectionConfirmation: () => void;
  fontInformation?: IFontInformation;
  onAddCorrections?: StateCallbacks["onAddCorrections"];
  onSelectBlock?: (id: string, text: string) => void;
  pages: ReadonlyArray<Page>;
  selected: boolean;
  selectedCell: string | null;
}>;
