import React, { memo } from "react";

import { Content, ContentType } from "../../types";
import { InnodomImage as Image } from "./InnodomImage";
import { Table } from "./Table";
import { Text } from "./Text";
import { Props } from "./types";

export const InnodomContent = memo(({ content, ...remaining }: Props<Content>) => {
  switch (content.type) {
    case ContentType.Image:
    case ContentType.Math:
      return <Image {...remaining} content={content} onAddCorrections={undefined} />;
    case ContentType.Table:
      return <Table {...remaining} content={content} />;
    case ContentType.Text:
      return <Text {...remaining} content={content} />;
  }
});
InnodomContent.displayName = "InnodomContent";
