import React, { memo } from "react";
import { Col, Row } from "reactstrap";

interface IProps {
  index: number;
  label: string;
}

export const QuestionTitle = memo(({ index, label }: IProps) => (
  <Row>
    <Col className="question-title" title={label} xs={12}>
      {index}. {label}
    </Col>
  </Row>
));
QuestionTitle.displayName = "QuestionTitle";
