import cx from "classnames";
import moment from "moment";
import React, { memo, useCallback, useEffect, useMemo, useReducer, useState } from "react";
import { Prompt } from "react-router";
import { Col, Row } from "reactstrap";

import "react-widgets/dist/css/react-widgets.css";

import "./styles.scss";

import { ATTR_ID } from "@ui/components/InnodomContent/constants";
import { ActionToolbar } from "./ActionToolbar";
import { TAG_CONTENT } from "./constants";
import { Document } from "./Document";
import { browserSupportsEditing, stripNamespaceAttributes } from "./Document/InnodomContent/util";
import { Questions } from "./Questions";
import { reducer, reviewCallbacks, useStateCallbacks } from "./state";
import { areAnswersValid } from "./state/areAnswersValid";
import { getDefaultState } from "./state/getDefaultState";
import { insertDataPoints } from "./state/insertDataPoints";
import { validateTaxonomy } from "./taxonomy";
import { Block, ITaxonomy, PageMeta, State } from "./types";

type Props = Readonly<{
  imageUris: Array<string>;
  onComplete: (xml: string) => Promise<void>;
  onReset: () => Promise<void>;
  onSave: (xml: string) => Promise<void>;
  pageMeta: Array<PageMeta>;
  rawTaxonomy: any;
  review: boolean;
  saving: boolean;
  xml: string;
}>;

moment.locale("en");
// tslint:disable-next-line: no-var-requires
const momentLocalizer = require("react-widgets-moment");
momentLocalizer();

const CONFIRM_RESET_MESSAGE =
  "Are you sure you want to reset? You will lose all your existing work.";
const CONFIRM_SUBMIT_MESSAGE = "Are you sure you want to submit your work?";
const CONFIRM_SUBMIT_OVERRIDE_MESSAGE =
  "Some questions have not been answered or are invalid. Are you really sure you want to submit your work?";

const getOutputXml = (oldXml: string, state: State, taxonomy: ITaxonomy): string => {
  // parse the XML again but replace the contents
  const parser = new DOMParser();
  const serializer = new XMLSerializer();
  const xmlDoc = parser.parseFromString(oldXml, "text/xml");
  if (Object.keys(state.textCorrections).length > 0) {
    const content = Array.from(xmlDoc.getElementsByTagName(TAG_CONTENT)[0].childNodes);
    for (const contentTag of content) {
      if (contentTag instanceof Element) {
        const contentId = contentTag.getAttribute(ATTR_ID);
        if (!!contentId && contentId in state.textCorrections) {
          const blocks: Array<Block> = state.content[contentId].blocks;
          contentTag.innerHTML = blocks
            .map((b, i) => {
              const clone = b.element.cloneNode() as Element;
              clone.innerHTML = state.textCorrections[contentId][i];
              return clone.outerHTML;
            })
            .join("");
        }
      }
    }
    stripNamespaceAttributes(xmlDoc.documentElement);
  }
  // insert the data points
  insertDataPoints(xmlDoc, state.dataPoints, taxonomy);
  return serializer.serializeToString(xmlDoc);
};

// auto save every 30 seconds
const AUTO_SAVE_INTERVAL = 30 * 1000;

const MappingComponent = ({
  imageUris,
  onReset,
  onComplete,
  onSave,
  pageMeta,
  rawTaxonomy,
  review,
  saving,
  xml,
}: Props) => {
  const taxonomy = useMemo(() => validateTaxonomy(rawTaxonomy), [rawTaxonomy]);
  const defaultState = useMemo(() => getDefaultState(taxonomy, xml, imageUris, pageMeta), [
    taxonomy,
    xml,
  ]);
  const [state, dispatch] = useReducer(reducer, defaultState);
  const stateCallbacks = review ? reviewCallbacks(dispatch) : useStateCallbacks(dispatch);
  const disableEditing = useMemo(() => !browserSupportsEditing(), []);
  const [tempSaving, setTempSaving] = useState(false);
  const onCompleteAction = useCallback(async (): Promise<void> => {
    if (!window.confirm(CONFIRM_SUBMIT_MESSAGE)) {
      return;
    }
    const answersValid = areAnswersValid(state.dataPoints, taxonomy);
    if (!answersValid) {
      if (!window.confirm(CONFIRM_SUBMIT_OVERRIDE_MESSAGE)) {
        return;
      }
    }
    if (!!autoSaveTimer) {
      clearTimeout(autoSaveTimer);
      setAutoSaveTimer(undefined);
    }
    onComplete(getOutputXml(xml, state, taxonomy));
  }, [state, taxonomy, xml]);
  const onResetAction = useCallback(async (): Promise<void> => {
    if (!window.confirm(CONFIRM_RESET_MESSAGE)) {
      return;
    }
    onReset();
  }, [onReset]);
  const onSaveAction = useCallback(async (): Promise<void> => {
    if (!state.modified) {
      return;
    }
    if (!!autoSaveTimer) {
      clearTimeout(autoSaveTimer);
      setAutoSaveTimer(undefined);
    }
    setTempSaving(true);
    setTimeout(async () => {
      await onSave(getOutputXml(xml, state, taxonomy));
      stateCallbacks.onMarkDataAsSaved();
      setTempSaving(false);
    }, 0);
  }, [onSave, setTempSaving, state, stateCallbacks.onMarkDataAsSaved, taxonomy, xml]);
  // implement an auto save function
  const [autoSaveTimer, setAutoSaveTimer] = useState<ReturnType<typeof setInterval> | undefined>(
    undefined,
  );
  useEffect(() => {
    if (state.modified) {
      if (!autoSaveTimer) {
        setAutoSaveTimer(
          setTimeout(() => {
            setTempSaving(true);
            setTimeout(async () => {
              await onSave(getOutputXml(xml, state, taxonomy));
              stateCallbacks.onMarkDataAsSaved();
              setTempSaving(false);
            }, 0);
          }, AUTO_SAVE_INTERVAL),
        );
      }
    } else if (!!autoSaveTimer) {
      clearTimeout(autoSaveTimer);
      setAutoSaveTimer(undefined);
    }
    return () => {
      if (!!autoSaveTimer) {
        clearTimeout(autoSaveTimer);
      }
    };
  });
  return (
    <>
      <Prompt
        message="You have unsaved changes. Are you sure you want to leave the page?"
        when={state.modified}
      />
      <Row className={cx("h-100", { review })}>
        <Col className="h-100 pr-0" xs={7}>
          <Document
            disableEditing={disableEditing}
            review={review}
            state={state}
            stateCallbacks={stateCallbacks}
            taxonomy={taxonomy}
          />
        </Col>
        <Col className="border-left datapoints-wrapper d-flex flex-column h-100 pl-0" xs={5}>
          {!review && (
            <ActionToolbar
              canSave={state.modified}
              disabled={!!saving}
              onComplete={onCompleteAction}
              onReset={onResetAction}
              onSave={onSaveAction}
              saving={tempSaving}
            />
          )}
          <Questions
            activeDataPoint={state.activeDataPoint}
            dataPoints={state.dataPoints}
            keyOrder={state.keyOrder}
            review={review}
            stateCallbacks={stateCallbacks}
            taxonomy={taxonomy}
          />
        </Col>
      </Row>
    </>
  );
};
export const Mapping = memo(MappingComponent);
Mapping.displayName = "Mapping";
