import React, { useCallback } from "react";

import { IJob } from "@core/api-types/entities/job";
import { ContentLoader } from "@ui/components/util/ContentLoader";
import { Loading } from "@ui/components/util/Loading";
import { Mapping } from "../../src/components/Mapping";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

const CONTENT_ROLE_LIST = [
  "mapping-reviewed-xml",
  "mapping-predicted-xml",
  "mapping-taxonomy-json",
  "zoning-pagehr-png",
  "zoning-pagesmeta-json",
];

export const MappingReview = ({ job, token }: Props) => {
  const dummyAction = useCallback(async (): Promise<void> => {
    console.warn("This should never be called.");
  }, []);
  const contentList = job.contents.filter(c => CONTENT_ROLE_LIST.indexOf(c.role) >= 0);
  return (
    <ContentLoader auth={token} content={contentList}>
      {({ contents, failed, progress, ready }) => {
        if (failed) {
          const error = new Error(
            "Failed to load job. Please report this error to a system administrator.",
          );
          return <Loading error={error} text={"Job failed to load."} />;
        }
        if (!ready) {
          return <Loading progress={progress} text={"Fetching job contents."} />;
        }
        const taxonomy = contents.find(c => c.role === "mapping-taxonomy-json")?.contents;
        const xmlContent = contents.find(c => c.role === "mapping-reviewed-xml")?.contents;
        const meta = contents
          .find(c => c.role === "zoning-pagesmeta-json")
          ?.contents.pages.map((p: any) => p.images);
        const imageUris: Array<string> = contents
          .filter(c => c.role === "zoning-pagehr-png")
          .map(c => c.contents as string);
        return (
          <Mapping
            imageUris={imageUris}
            key={job.id}
            onComplete={dummyAction}
            onReset={dummyAction}
            onSave={dummyAction}
            pageMeta={meta}
            rawTaxonomy={taxonomy}
            review
            saving={false}
            xml={xmlContent}
          />
        );
      }}
    </ContentLoader>
  );
};
MappingReview.displayName = "MappingReview";
