import cx from "classnames";
import React from "react";

import { StateCallbacks } from "../../state";
import { ContentType, State } from "../../types";
import { InnodomContent } from "../InnodomContent";
import { tableContainsCellRef } from "../InnodomContent/util";
import { ScrollToElement } from "../ScrollToElement";

type Props = Readonly<{
  activeBlock: string | null;
  activeDataPoint: State["activeDataPoint"];
  confirmTableSelection: boolean;
  content: State["content"];
  contentOrder: State["contentOrder"];
  corrections: State["textCorrections"];
  disableEditing: boolean;
  disableTableSelectionConfirmation: () => void;
  fontInformation: State["fontInformation"];
  onAddCorrections: StateCallbacks["onAddCorrections"];
  onSelectBlock: (id: string, text: string) => void;
  pages: State["pages"];
}>;

export const Text = ({
  activeBlock,
  activeDataPoint,
  confirmTableSelection,
  content,
  contentOrder,
  corrections,
  disableEditing,
  disableTableSelectionConfirmation,
  fontInformation,
  onAddCorrections,
  onSelectBlock,
  pages,
}: Props) => {
  const selector = activeBlock === null ? null : `support-${activeBlock}`;
  const wrapperClassName = cx("blocks", "h-100", "overflow-auto", "p-3", {
    selecting: activeDataPoint.groupKey !== "",
  });
  return (
    <ScrollToElement
      activeDataPoint={activeDataPoint}
      className={wrapperClassName}
      selector={selector}
    >
      {contentOrder.map(contentId => {
        const contentBlock = content[contentId];
        if (!contentBlock) {
          return null;
        }
        const selected = !!activeBlock && activeBlock === contentBlock.id;
        let selectedCell = null;
        if (
          activeBlock &&
          contentBlock.type === ContentType.Table &&
          tableContainsCellRef(contentBlock, activeBlock)
        ) {
          selectedCell = activeBlock;
        }
        return (
          <InnodomContent
            confirmTableSelection={confirmTableSelection}
            content={contentBlock}
            corrections={corrections}
            disableTableSelectionConfirmation={disableTableSelectionConfirmation}
            fontInformation={fontInformation}
            key={`block-${contentBlock.id}`}
            onAddCorrections={disableEditing ? undefined : onAddCorrections}
            onSelectBlock={onSelectBlock}
            pages={pages}
            selected={selected}
            selectedCell={selectedCell}
          />
        );
      })}
    </ScrollToElement>
  );
};
Text.displayName = "Text";
