import React from "react";

import { Row as RowType, State, TableContent } from "../../../types";
import { Props as ContentProps } from "../types";
import { Cell } from "./Cell";

type Props = Readonly<{
  fontInformation: State["fontInformation"];
  isHeader: boolean;
  row: RowType;
  onSelectBlock: ContentProps<TableContent>["onSelectBlock"];
  selectedCell: string | null;
}>;

export const Row = ({ fontInformation, isHeader, onSelectBlock, selectedCell, row }: Props) => (
  <tr>
    {row.cells.map((cell, cellIndex) => (
      <Cell
        cell={cell}
        fontInformation={fontInformation}
        isHeader={isHeader}
        key={`cell-${cellIndex}`}
        onSelectBlock={onSelectBlock}
        selectedCell={selectedCell}
      />
    ))}
  </tr>
);
Row.displayName = "Row";
