import cx from "classnames";
import React, { useState } from "react";
import { Button } from "reactstrap";

import { TableContent } from "../../../types";
import { Props } from "../types";
import { Editing } from "./Editing";
import { Table as InnerTable } from "./Table";

export const TABLE_SELECT_CONFIRMATION = [
  "Are you sure you want to select an entire table as support?",
  "It is more desireable to select a specific cell.",
].join(" ");

export const Table = ({
  confirmTableSelection,
  content,
  corrections,
  disableTableSelectionConfirmation,
  fontInformation,
  onAddCorrections,
  onSelectBlock,
  pages,
  selected,
  selectedCell,
}: Props<TableContent>) => {
  const [mouseWithin, setMouseWithin] = useState(false);
  const [editing, setEditing] = useState(false);
  if (!!onAddCorrections && editing) {
    return (
      <Editing
        content={content}
        corrections={corrections}
        onCancel={() => {
          setEditing(false);
        }}
        onSave={(newCorrections: Array<string>) => {
          setEditing(false);
          onAddCorrections(content.id, newCorrections);
        }}
        onSelectBlock={onSelectBlock}
        pages={pages}
      />
    );
  }
  return (
    <div
      className={cx("block", "pt-3", "pb-3", {
        editable: !!onAddCorrections,
        "p-3": !onAddCorrections,
        selected,
        within: mouseWithin,
      })}
      onClick={() => {
        if (mouseWithin || !onSelectBlock) {
          return;
        }
        if (confirmTableSelection && !window.confirm(TABLE_SELECT_CONFIRMATION)) {
          return;
        }
        disableTableSelectionConfirmation();
        onSelectBlock(content.id, content.text);
      }}
    >
      {!!onAddCorrections && (
        <Button
          className="btn-edit"
          color="link"
          onClick={e => {
            e.stopPropagation();
            setEditing(true);
          }}
          title="Edit"
        >
          <i className="fa fa-edit" />
          <span className="sr-only">Edit</span>
        </Button>
      )}
      <InnerTable
        content={content}
        corrections={corrections && content.id in corrections ? corrections[content.id] : undefined}
        fontInformation={fontInformation}
        id={content.id}
        onSelectBlock={onSelectBlock}
        selected={selected}
        selectedCell={selectedCell}
        setMouseWithin={setMouseWithin}
      />
    </div>
  );
};
Table.displayName = "Table";
