import cx from "classnames";
import React, { useCallback, useState } from "react";
import { Button, Col, Form, FormFeedback, FormGroup, Input } from "reactstrap";

import { escape } from "@ui/util";
import { ATTR_ID } from "../../../constants";
import { State, TableBlock, TableContent } from "../../../types";
import {
  ATTR_COL,
  ATTR_COLSPAN,
  ATTR_MAX_X,
  ATTR_MAX_Y,
  ATTR_MIN_X,
  ATTR_MIN_Y,
  ATTR_ROW,
  ATTR_ROWSPAN,
  TAG_COL,
  TAG_ROW,
} from "../constants";
import { InnodomImage } from "../InnodomImage";
import { Props as ContentProps } from "../types";
import { extractFragmentFromBlock, insertFragmentIntoBlock } from "../util";

type Props = Readonly<{
  content: ContentProps<TableContent>["content"];
  corrections?: State["textCorrections"];
  onCancel: () => void;
  onSave: (corrections: Array<string>) => void;
  onSelectBlock: ContentProps<TableContent>["onSelectBlock"];
  pages: ContentProps<TableContent>["pages"];
}>;

const extractTableFragmentFromBlock = (block: Element): string => {
  const ignoreRowAttrs = [ATTR_ROW, "xmlns:inno"];
  const ignoreColAttrs = [
    ATTR_COL,
    ATTR_ID,
    ATTR_MAX_X,
    ATTR_MIN_X,
    ATTR_MAX_Y,
    ATTR_MIN_Y,
    "xmlns:inno",
  ];
  const toReturn: Array<string> = [];
  const rows = Array.from(block.getElementsByTagName(TAG_ROW));
  for (const row of rows) {
    const rowAttrs = Array.from(row.attributes)
      .filter(attr => ignoreRowAttrs.indexOf(attr.name) < 0)
      .map(attr => `${attr.name}="${escape(attr.value)}"`);
    let rowAttrString = "";
    if (rowAttrs.length > 0) {
      rowAttrString = " " + rowAttrs.join(" ");
    }
    const cols = Array.from(row.getElementsByTagName(TAG_COL));
    if (cols.length < 1) {
      toReturn.push(`<tr${rowAttrString}/>`);
      continue;
    }
    const columns: Array<string> = cols.map((col: Element): string => {
      const colAttrs = Array.from(col.attributes)
        .filter(attr => ignoreColAttrs.indexOf(attr.name) < 0)
        .filter(attr => {
          if (attr.name === ATTR_COLSPAN || attr.name === ATTR_ROWSPAN) {
            const spanValue = parseInt(attr.value, 10);
            if (isNaN(spanValue) || spanValue === 1) {
              return false;
            }
          }
          return true;
        })
        .map(attr => `${attr.name}="${escape(attr.value)}"`);
      let colAttrString = "";
      if (colAttrs.length > 0) {
        colAttrString = " " + colAttrs.join(" ");
      }
      return `  <td${colAttrString}>\n${extractFragmentFromBlock(col, "    ")}\n  </td>`;
    });
    toReturn.push(`<tr${rowAttrString}>\n${columns.join("\n")}\n</tr>`);
  }
  return toReturn.join("\n");
};

const injectTableAttributes = (block: Element, tableId: string): Element => {
  const rows = block.getElementsByTagName(TAG_ROW);
  for (let i = 0; i < rows.length; i++) {
    rows[i].setAttribute(ATTR_ROW, `${i + 1}`);
    const cols = rows[i].getElementsByTagName(TAG_COL);
    for (let j = 0; j < cols.length; j++) {
      cols[j].setAttribute(ATTR_COL, `${j + 1}`);
      cols[j].setAttribute(ATTR_ID, `${tableId}-${i + 1}-${j + 1}`);
      let colSpan = parseInt(cols[j].getAttribute(ATTR_COLSPAN) || "", 10);
      if (isNaN(colSpan) || colSpan < 1) {
        colSpan = 1;
      }
      let rowSpan = parseInt(cols[j].getAttribute(ATTR_ROWSPAN) || "", 10);
      if (isNaN(rowSpan) || rowSpan < 1) {
        rowSpan = 1;
      }
      cols[j].setAttribute(ATTR_COLSPAN, `${colSpan}`);
      cols[j].setAttribute(ATTR_ROWSPAN, `${rowSpan}`);
    }
  }
  return block;
};

export const Editing = ({ content, corrections, onCancel, onSave, pages }: Props) => {
  let initialState = [];
  if (!!corrections && content.id in corrections) {
    initialState = corrections[content.id].map((c, i) => {
      const block = content.blocks[i].element.cloneNode() as Element;
      block.innerHTML = c;
      return extractTableFragmentFromBlock(block);
    });
  } else {
    initialState = content.blocks.map(b => extractTableFragmentFromBlock(b.element));
  }
  const [contentText, setContentText] = useState<Array<string>>(initialState);
  const [xmlValid, setXmlValid] = useState(true);
  const dummyTableCorrection = useCallback(() => {
    // do nothing
  }, []);
  return (
    <Form
      className="border bg-light p-3 mb-2"
      onSubmit={e => {
        e.preventDefault();
        onSave(
          contentText.map((t, i) => {
            const block = injectTableAttributes(
              insertFragmentIntoBlock(content.blocks[i].element.cloneNode() as Element, t),
              content.id,
            );
            return block.innerHTML;
          }),
        );
      }}
    >
      {content.blocks.map((cb: TableBlock, i) => (
        <FormGroup className="mb-0" key={`block-${i}`} row>
          <Col>
            <div className="m-auto">
              <InnodomImage
                confirmTableSelection={false}
                content={content as any}
                disableTableSelectionConfirmation={dummyTableCorrection}
                pages={pages}
                selected={false}
                selectedCell={null}
              />
            </div>
            <Input
              autoFocus
              className="d-block mb-2"
              invalid={!xmlValid}
              onChange={e => {
                setContentText([
                  ...contentText.slice(0, i),
                  e.target.value,
                  ...contentText.slice(i + 1),
                ]);
                try {
                  const clone = cb.element.cloneNode() as Element;
                  insertFragmentIntoBlock(clone, e.target.value);
                  setXmlValid(true);
                } catch (err) {
                  setXmlValid(false);
                }
              }}
              onKeyDown={e => {
                if (e.key === "Escape") {
                  e.preventDefault();
                  e.stopPropagation();
                  onCancel();
                }
              }}
              rows={6}
              type="textarea"
              valid={xmlValid}
              value={contentText[i]}
            />
          </Col>
        </FormGroup>
      ))}
      <div className="d-flex justify-content-between align-items-center">
        <Button color="secondary" onClick={onCancel} type="button">
          Cancel
        </Button>
        <FormFeedback
          className="d-block flex-fill text-center"
          invalid={!xmlValid}
          valid={xmlValid}
        >
          <i className={cx("fa", "mr-2", { "fa-check": xmlValid, "fa-times": !xmlValid })} />
          {xmlValid ? "XML is valid" : "XML is invalid"}
        </FormFeedback>
        <Button color="primary" disabled={!xmlValid} type="submit">
          Save
        </Button>
      </div>
    </Form>
  );
};
Editing.displayName = "Editing";
