import { InnodomDateFormats, WorkbenchDateFormats } from "@ui/util/dateFormats";
import moment from "moment";
import React, { ReactNode, useCallback, useState } from "react";
import { StateCallbacks } from "../state";
import { IDataPoint, IDataPointGroup, IDataPointValue, State, TaxonomyEntryType } from "../types";

interface IProps {
  children: ReactNode;
  onSelectText: (text: string) => void;
}

const findBlockId = (node: Node): string | null => {
  let parent: HTMLElement | null = node.parentElement;
  while (!!parent) {
    if (parent.classList.contains("block")) {
      return parent.getAttribute("id");
    }
    parent = parent.parentElement;
  }
  return null;
};

export const TextSelection = (props: IProps) => {
  const [selecting, setSelecting] = useState(false);
  const updateSelection = () => {
    if (!selecting) {
      return;
    }
    const selection = window.getSelection();
    if (
      selection === null ||
      selection.isCollapsed ||
      selection.anchorNode === null ||
      selection.focusNode === null
    ) {
      return;
    }
    if (findBlockId(selection.anchorNode) !== findBlockId(selection.focusNode)) {
      return;
    }
    props.onSelectText(selection.toString().trim());
  };
  return (
    <div
      onMouseDown={() => {
        setSelecting(true);
      }}
      onMouseLeave={updateSelection}
      onMouseUp={() => {
        updateSelection();
        setSelecting(false);
      }}
    >
      {props.children}
    </div>
  );
};
TextSelection.displayName = "TextSelection";

export const onSelectText = (
  activeDataPoint: IDataPoint | IDataPointGroup | null,
  activeValue: IDataPointValue<any> | null,
  selectedDataPoint: State["activeDataPoint"],
  stateCallbacks: StateCallbacks,
) =>
  useCallback(
    (text: string) => {
      if (activeDataPoint === null || activeValue === null) {
        return;
      }
      let value: any = null;
      let valueRaw: string = "";
      switch (activeDataPoint.type) {
        // we only support text selection for these types
        case TaxonomyEntryType.Number: {
          const numericValue = parseFloat(text.replace(/\s|\$|%|,/g, ""));
          if (!isNaN(numericValue)) {
            value = numericValue;
            valueRaw = numericValue.toString(10);
          }
          break;
        }
        case TaxonomyEntryType.Date:
        case TaxonomyEntryType.DateTime:
        case TaxonomyEntryType.Time: {
          const parsed = moment(text, WorkbenchDateFormats.allSupported);
          if (parsed.isValid()) {
            value = parsed.toDate();
            valueRaw = parsed.format(InnodomDateFormats[activeDataPoint.type]);
          }
          break;
        }
        case TaxonomyEntryType.Selection:
        case TaxonomyEntryType.String:
          value = text;
          valueRaw = text;
          break;
        default:
          return;
      }
      if (selectedDataPoint.groupKey !== selectedDataPoint.key) {
        stateCallbacks.onUpdateGroupedAnswer(
          selectedDataPoint.groupKey,
          selectedDataPoint.groupIndex,
          selectedDataPoint.key,
          selectedDataPoint.index,
          value,
          valueRaw,
        );
      } else {
        stateCallbacks.onUpdateNonGroupedAnswer(
          selectedDataPoint.key,
          selectedDataPoint.index,
          value,
          valueRaw,
        );
      }
    },
    [activeDataPoint, activeValue, stateCallbacks],
  );
