import { IFontInformation } from "./Document/InnodomContent/types";

export enum TaxonomyEntryType {
  Boolean = "boolean",
  Date = "date",
  DateTime = "datetime",
  Enum = "enum",
  Group = "group",
  Number = "number",
  Pointer = "pointer",
  Selection = "selection",
  String = "string",
  Time = "time",
}

interface IBaseTaxonomyEntry {
  description?: string;
  groups?: Array<string>;
  multiple: boolean;
  question: string;
  required: boolean;
  type: TaxonomyEntryType;
}
export interface IBooleanTaxonomyEntry extends IBaseTaxonomyEntry {
  type: TaxonomyEntryType.Boolean;
}
export interface IDateTaxonomyEntry extends IBaseTaxonomyEntry {
  min?: Date;
  max?: Date;
  type: TaxonomyEntryType.Date;
}
export interface IDateTimeTaxonomyEntry extends IBaseTaxonomyEntry {
  min?: Date;
  max?: Date;
  type: TaxonomyEntryType.DateTime;
}
export interface IEnumTaxonomyEntry extends IBaseTaxonomyEntry {
  options: Array<string>;
  type: TaxonomyEntryType.Enum;
}
export interface INumberTaxonomyEntry extends IBaseTaxonomyEntry {
  min?: number;
  max?: number;
  type: TaxonomyEntryType.Number;
}
export interface IPointerTaxonomyEntry extends IBaseTaxonomyEntry {
  type: TaxonomyEntryType.Pointer;
}
export interface ISelectionTaxonomyEntry extends IBaseTaxonomyEntry {
  maxLength?: number;
  minLength?: number;
  type: TaxonomyEntryType.Selection;
}
export interface IStringTaxonomyEntry extends IBaseTaxonomyEntry {
  maxLength?: number;
  minLength?: number;
  type: TaxonomyEntryType.String;
}
export interface ITimeTaxonomyEntry extends IBaseTaxonomyEntry {
  type: TaxonomyEntryType.Time;
}
export interface IGroupTaxonomyEntry {
  description: string;
  multiple: boolean;
  required: boolean;
  type: TaxonomyEntryType.Group;
}
export type TaxonomyEntry =
  | IBooleanTaxonomyEntry
  | IDateTaxonomyEntry
  | IDateTimeTaxonomyEntry
  | IEnumTaxonomyEntry
  | IGroupTaxonomyEntry
  | INumberTaxonomyEntry
  | IPointerTaxonomyEntry
  | ISelectionTaxonomyEntry
  | IStringTaxonomyEntry
  | ITimeTaxonomyEntry;

export interface ITaxonomy {
  [id: string]: TaxonomyEntry;
}

export interface IDataPointValue<T> {
  readonly confidence: number;
  readonly idRef: string | null;
  readonly value: T;
  readonly valueRaw: string;
}

export interface IBaseDataPoint {
  readonly isGroup: boolean;
  readonly key: string;
  readonly label: string;
  readonly multiple: boolean;
  readonly type: TaxonomyEntryType;
}

export interface IDataPoint extends IBaseDataPoint {
  readonly isGroup: false;
  readonly values: Array<IDataPointValue<any>>;
}

export interface IDataPointGroupValue {
  [key: string]: Array<IDataPointValue<any>>;
}
export interface IDataPointGroup extends IBaseDataPoint {
  readonly collapsedValues: Array<number>;
  readonly dataPoints: {
    [key: string]: IBaseDataPoint & { isGroup: false };
  };
  readonly isGroup: true;
  readonly keyOrder: ReadonlyArray<string>;
  readonly type: TaxonomyEntryType.Group;
  readonly values: Array<IDataPointGroupValue>;
}

export enum ContentType {
  Image = "image",
  Math = "math",
  Table = "table",
  Text = "text",
}

type BaseBlock<T extends ContentType> = Readonly<{
  contentId: string;
  contentIndex: number;
  element: Element;
  height: number;
  page: number;
  type: T;
  width: number;
  x: number;
  y: number;
}>;
export type ImageBlock = BaseBlock<ContentType.Image>;
export type MathBlock = BaseBlock<ContentType.Math>;
export type Cell = Readonly<{
  col: number;
  colSpan: number;
  element: Element;
  height: number;
  id: string;
  rowSpan: number;
  text: string;
  width: number;
  x: number;
  y: number;
}>;

export type Row = Readonly<{
  cells: Array<Cell>;
  row: number;
}>;
export type TableBlock = BaseBlock<ContentType.Table> &
  Readonly<{
    rows: Array<Row>;
  }>;
export type TextBlock = BaseBlock<ContentType.Text>;
export type Block = ImageBlock | MathBlock | TableBlock | TextBlock;

type BaseContent<T extends ContentType, B extends Block> = Readonly<{
  blocks: Array<B>;
  id: string;
  type: T;
}>;
export type ImageContent = BaseContent<ContentType.Image, ImageBlock>;
export type MathContent = BaseContent<ContentType.Math, MathBlock>;
export type TableContent = BaseContent<ContentType.Table, TableBlock> &
  Readonly<{
    text: string;
  }>;
export type TextContent = BaseContent<ContentType.Text, TextBlock> &
  Readonly<{
    text: string;
  }>;
export type Content = ImageContent | MathContent | TableContent | TextContent;

export type PageMeta = Readonly<{
  height: number;
  width: number;
}>;

export type Page = Readonly<{
  backgroundSrc: string;
  blocks: Array<Block>;
  height: number;
  width: number;
}>;

export type State = Readonly<{
  activeDataPoint: {
    groupIndex: number;
    groupKey: string;
    index: number;
    key: string;
  };
  content: Readonly<{
    [id: string]: Content;
  }>;
  contentOrder: ReadonlyArray<string>;
  dataPoints: {
    [key: string]: IDataPoint | IDataPointGroup;
  };
  fontInformation?: IFontInformation;
  keyOrder: ReadonlyArray<string>;
  modified: boolean;
  pages: ReadonlyArray<Page>;
  textCorrections: Readonly<{
    [id: string]: Array<string>;
  }>;
}>;
