import cx from "classnames";
import React, { useState } from "react";
import { Button } from "reactstrap";

import { TextContent } from "../../../types";
import { Props } from "../types";
import { browserSupportsEditing, extractTextBlock } from "../util";
import { Editing } from "./Editing";

export const Text = ({
  content,
  corrections,
  fontInformation,
  onAddCorrections,
  onSelectBlock,
  pages,
  selected,
}: Props<TextContent>) => {
  const [editing, setEditing] = useState(false);
  const { blocks } = content;
  if (editing && !!onAddCorrections) {
    return (
      <Editing
        content={content}
        corrections={corrections}
        onCancel={() => {
          setEditing(false);
        }}
        onSave={(newCorrections: Array<string>) => {
          setEditing(false);
          onAddCorrections(content.id, newCorrections);
        }}
        pages={pages}
      />
    );
  }
  return (
    <p
      className={cx("block", { editable: !!onAddCorrections, selected })}
      id={`support-${content.id}`}
      onClick={() => {
        if (!onSelectBlock) {
          return;
        }
        onSelectBlock(content.id, content.text);
      }}
    >
      {!!onAddCorrections && (
        <Button
          className="btn-edit"
          color="link"
          onClick={e => {
            e.stopPropagation();
            if (!browserSupportsEditing()) {
              window.alert("Editing contents are unsupported on this browser.");
              return;
            }
            setEditing(true);
          }}
          title="Edit"
        >
          <i className="fa fa-edit" />
          <span className="sr-only">Edit</span>
        </Button>
      )}
      {blocks.map((b, index) => {
        let renderedElement = b.element;
        if (!!corrections && content.id in corrections) {
          renderedElement = b.element.cloneNode() as Element;
          renderedElement.innerHTML = corrections[content.id][index];
        }
        return extractTextBlock(renderedElement, `${content.id}-block-${index}-`, fontInformation);
      })}
    </p>
  );
};
Text.displayName = "Text";
