import cx from "classnames";
import React, { useRef } from "react";
import { ListGroup } from "reactstrap";

import { StateCallbacks } from "../state";
import { ITaxonomy, State } from "../types";
import { Question } from "./Question";
import { useScrollToGroup } from "./useScrollToGroup";

type Props = Readonly<{
  activeDataPoint: State["activeDataPoint"];
  dataPoints: State["dataPoints"];
  keyOrder: State["keyOrder"];
  review: boolean;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

export const Questions = ({
  activeDataPoint,
  dataPoints,
  keyOrder,
  review,
  stateCallbacks,
  taxonomy,
}: Props) => {
  const questionListRef = useRef(null);
  useScrollToGroup(activeDataPoint.groupKey, questionListRef);
  return (
    <div className="h-100 overflow-auto" ref={questionListRef}>
      <ListGroup className={cx({ "border-top": !review, "border-left": review })} flush>
        {keyOrder.map((key, i) => {
          if (!(key in dataPoints)) {
            console.warn(`Key: ${key} not found in data points.`);
            return null;
          }
          return (
            <Question
              active={activeDataPoint}
              dataPoint={dataPoints[key]}
              key={key}
              index={i + 1}
              review={review}
              stateCallbacks={stateCallbacks}
              taxonomy={taxonomy}
            />
          );
        })}
      </ListGroup>
    </div>
  );
};
Questions.displayName = "Questions";
