import React, { useEffect, useRef } from "react";
import { Button, ButtonGroup, ButtonToolbar } from "reactstrap";

interface IProps {
  canSave: boolean;
  disabled: boolean;
  onComplete: () => Promise<void>;
  onReset: () => Promise<void>;
  onSave: () => Promise<void>;
  saving: boolean;
}

export const ActionToolbar = ({
  canSave,
  disabled,
  onComplete,
  onReset,
  onSave,
  saving,
}: IProps) => {
  const onSaveRef = useRef<HTMLButtonElement>(null);
  useEffect(() => {
    const keyDownListener = (e: KeyboardEvent) => {
      if (onSaveRef.current && e.key === "s" && e.ctrlKey) {
        e.preventDefault();
        onSaveRef.current.click();
      }
    };
    document.addEventListener("keydown", keyDownListener);
    return () => {
      document.removeEventListener("keydown", keyDownListener);
    };
  }, [onSaveRef.current]);
  let saveButtonContents = (
    <>
      <i className="fa fa-check mr-2" />
      Saved
    </>
  );
  if (saving) {
    saveButtonContents = (
      <>
        <i className="fa fa-spin fa-spinner mr-2" />
        Saving
      </>
    );
  } else if (canSave) {
    saveButtonContents = <>Save</>;
  }
  return (
    <ButtonToolbar className="actions d-flex justify-content-between ml-2 mr-2 p-2">
      <ButtonGroup>
        <Button
          color="danger"
          disabled={disabled}
          onClick={e => {
            e.preventDefault();
            onReset();
          }}
          type="reset"
        >
          Reset
        </Button>
      </ButtonGroup>
      <div className="align-items-center">
        <ButtonGroup className="align-items-center">
          <Button
            color="success"
            disabled={disabled || saving || !canSave}
            innerRef={onSaveRef}
            onClick={onSave}
            title="Ctrl + S"
            type="button"
          >
            {saveButtonContents}
          </Button>
        </ButtonGroup>
      </div>
      <ButtonGroup>
        <Button
          color="primary"
          disabled={disabled}
          onClick={e => {
            e.preventDefault();
            onComplete();
          }}
          type="submit"
        >
          Submit
        </Button>
      </ButtonGroup>
    </ButtonToolbar>
  );
};
ActionToolbar.displayName = "ActionToolbar";
